import React from 'react'
import Logo from "../../assets/images/logo.png"
import wave from "../../assets/images/wave.svg"
import stylesheet from "../../assets/css/stylesheet.css"
import { NavLink } from "react-router-dom";
export default function Header(props) {
    return (
        <>
            <nav class="navbar navbar-expand-lg navbar-light header">
                <a class="navbar-brand" href="#"><img src={Logo} className="logo" /></a>
                <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                    <span class="navbar-toggler-icon"></span>
                </button>

                <div class="collapse navbar-collapse" id="navbarSupportedContent">
                    <ul class="navbar-nav ml-auto">
                        <li class="nav-item active">
                            <NavLink class="nav-link" to="/">Home <span class="sr-only">(current)</span></NavLink>
                        </li>
                        <li class="nav-item">
                            <NavLink class="nav-link" to="/team">Team</NavLink>
                        </li>
                        <li class="nav-item">
                            <NavLink class="nav-link" to="/events">Events</NavLink>
                        </li>

                    </ul>

                </div>
            </nav>
            <div className="main">
                <div className="home-banner">
                    <div className="container">
                        <div className="banner-text2">
                            {/* <h1 className="text-center banner-text" >Cultivating leaders of tomorrow through action today</h1> */}
                            <center> <h1 className="text-center banner-text" >{props.team}</h1></center>
                        </div>
                    </div>
                </div>
                <svg class="wavy wavy-bottom banner-svg" viewBox="0 0 1920 150" fill="none" xmlns="http://www.w3.org/2000/svg">
                    <path fill-rule="evenodd" clip-rule="evenodd" d="M0 71.6344L80 57.5735C160 43.5126 320 15.3907 480 4.84502C640 -5.70068 800 1.32979 960 22.4212C1120 43.5126 1280 78.6649 1440 89.2106C1600 99.7563 1760 85.6954 1840 78.6649L1920 71.6344V156H1840C1760 156 1600 156 1440 156C1280 156 1120 156 960 156C800 156 640 156 480 156C320 156 160 156 80 156H0V71.6344Z" fill="white"></path>
                </svg>
            </div>





        </>
    )
}